import React, { useState, useEffect } from "react";
import axios from "axios";
import PokeModal from "./components/PokeModal";
// import PokeBerry from "./components/PokeBerry";
import { DataContext } from "./components/context/DataContext";
import { Spinner } from "react-bootstrap";
import NavbarComp from "./components/navbar/NavbarComp";

function App() {
  const [pokedex, setPokedex] = useState([]);
  const [wildPokemon, setWildPokemon] = useState({});
  const [storeCard, setStoreCard] = useState({});
  const [disableSpinner, setDisableSpinner] = useState(true);

  // const [wildBerry, setWildBerry] = useState({}); // ---

  useEffect(() => {
    encounterWildPokemon();
    // berries();
  }, []);

  const pokeId = () => {
    const min = Math.ceil(400);
    const max = Math.floor(480);
    return Math.floor(Math.random() * (max - min + 1)) + min;
  };

  const [loading, setLoading] = useState(true);

  const encounterWildPokemon = () => {
    setLoading(true);
    return axios
      .get(`https://pokeapi.co/api/v2/pokemon/${pokeId()}`)
      .then(response => {
        // response.data && console.log("Received the data");
        setWildPokemon(response.data);
        response.data && setLoading(false);
      });
  };

  //wildBerry.id && console.log(wildBerry); // MISTAKE: wildBerry is still true. Must be more specific, wildberry.id

  const catchPokemon = pokemon => {
    setPokedex(state => {
      const monExists = state.filter(p => pokemon.id === p.id).length > 0; // mostly false. Only true if you catch the same pokemon
      if (!monExists) {
        state = [...state, pokemon];
        state.sort(function(a, b) {
          return a.id - b.id;
        });
      }
      return state;
    });
    encounterWildPokemon(); // MISTAKE: we have to call this function whenever we're done
  };

  const releasePokemon = id => {
    setPokedex(state => state.filter(p => p.id !== id));
  };

  // PokeModal
  const [show, setShow] = useState(false);

  const handleClose = () => setShow(false);

  const handleShow = pokemon => {
    setShow(true);
    setStoreCard(pokemon);
    console.log("retrieved card");
  };

  // JSX
  return loading ? (
    <>
      <DataContext.Provider
        value={{ loading, setLoading, disableSpinner, setDisableSpinner }}
      >
        <NavbarComp />
      </DataContext.Provider>
      <div className="loading">
        {disableSpinner && <Spinner animation="grow" />}
      </div>
    </>
  ) : (
    <>
      {console.log("temp")}
      <DataContext.Provider
        value={{ loading, setLoading, disableSpinner, setDisableSpinner }}
      >
        <NavbarComp />
      </DataContext.Provider>
      <div className="app-wrapper container">
        <header>
          <h1 className="title">React Hooks</h1>
          <h3 className="subtitle">With Pokémon</h3>
        </header>
        <section className="wild-pokemon">
          <h2>Wild Encounter</h2>

          <img
            src={
              "https://raw.githubusercontent.com/PokeAPI/sprites/master/sprites/pokemon/" +
              wildPokemon.id +
              ".png"
            }
            className="sprite"
            alt=""
          />
          <h3>{wildPokemon.name}</h3>
          <button
            className="catch-btn"
            onClick={() => catchPokemon(wildPokemon)}
          >
            CATCH
          </button>
        </section>

        {/* Pokedex */}
        <section className="pokedex">
          <h2>Pokédex</h2>
          <p>Gotta catch 'em all</p>
          <div className="pokedex-list">
            {pokedex.map(pokemon => (
              <div
                className="pokemon"
                key={pokemon.id}
                onClick={() => handleShow(pokemon)}
              >
                <img
                  src={
                    "https://raw.githubusercontent.com/PokeAPI/sprites/master/sprites/pokemon/" +
                    pokemon.id +
                    ".png"
                  }
                  className="sprite"
                  alt=""
                />
                <h3 className="pokemon-name">{pokemon.name}</h3>
                <button
                  className="remove"
                  onClick={e => {
                    releasePokemon(pokemon.id);
                    e.stopPropagation();
                  }}
                >
                  &times;
                </button>
              </div>
            ))}

            <DataContext.Provider value={[storeCard]}>
              <PokeModal close={handleClose} reveal={show} card={storeCard} />
            </DataContext.Provider>
          </div>
        </section>
        {/* <PokeBerry /> */}
      </div>
      <div className="div_padding"></div>
    </>
  );
}

export default App;
